import { Grid } from '@mui/material'
import TeamsItem from '../TeamsItem/TeamsItem.jsx'

function TeamsList({teamsList}){


    return(
        <Grid container spacing={3} justifyContent="center">
          {/*  dailyWeather,   day
                  jsx */}
          {teamsList.map((team) => (
            <TeamsItem
                key={team.id} // 
                idTeam={team.id}
                nameTeam={team.name}
                flagTeam={team.crestUrl}
            />
          ))}
        </Grid>
    )

}

export default TeamsList;